// src/index.ts
import { BCS } from "aptos";
var MartianWalletName = "Martian";
var MartianWallet = class {
  constructor() {
    this.networkToChainId = {
      mainnet: 1,
      testnet: 2,
      devnet: 43
    };
    this.name = MartianWalletName;
    this.url = "https://chrome.google.com/webstore/detail/martian-wallet/efbglgofoippbgcjepnhiblaibcnclgk";
    this.icon = "data:image/png;base64,";
    this.provider = typeof window !== "undefined" ? window.martian : void 0;
  }
  async connect() {
    try {
      const addressInfo = await this.provider?.connect();
      if (!addressInfo)
        throw `${MartianWalletName} Address Info Error`;
      return addressInfo;
    } catch (error) {
      throw error;
    }
  }
  async account() {
    const response = await this.provider?.account();
    if (!response)
      throw `${MartianWalletName} Account Error`;
    return response;
  }
  async disconnect() {
    try {
      await this.provider?.disconnect();
    } catch (error) {
      throw error;
    }
  }
  async signAndSubmitTransaction(transaction, options) {
    try {
      const signer = await this.account();
      const tx = await this.provider?.generateTransaction(
        signer.address,
        transaction,
        { ...options, max_gas_amount: options?.max_gas_amount?.toString() }
      );
      if (!tx)
        throw new Error(
          "Cannot generate transaction"
        );
      const response = await this.provider?.signAndSubmitTransaction(tx);
      if (!response) {
        throw new Error("No response");
      }
      return { hash: response };
    } catch (error) {
      throw error;
    }
  }
  async signAndSubmitBCSTransaction(transaction, options) {
    try {
      const serializer = new BCS.Serializer();
      transaction.serialize(serializer);
      const response = await this.provider?.signAndSubmitBCSTransaction(
        serializer.getBytes().toString(),
        options
      );
      if (response.code) {
        throw new Error(response.message);
      }
      return { hash: response };
    } catch (error) {
      const errMsg = error.message;
      throw errMsg;
    }
  }
  async signTransaction(transaction, options) {
    try {
      const signer = await this.account();
      const tx = await this.provider?.generateTransaction(
        signer.address,
        transaction,
        options
      );
      if (!tx)
        throw new Error(
          "Cannot generate transaction"
        );
      const response = await this.provider?.signTransaction(tx);
      if (!response) {
        throw new Error("No response");
      }
      return response;
    } catch (error) {
      throw error;
    }
  }
  async signMessage(message) {
    try {
      if (typeof message !== "object" || !message.nonce) {
        `${MartianWalletName} Invalid signMessage Payload`;
      }
      const response = await this.provider?.signMessage(message);
      if (response) {
        return response;
      } else {
        throw `${MartianWalletName} Sign Message failed`;
      }
    } catch (error) {
      const errMsg = error.message;
      throw errMsg;
    }
  }
  async onNetworkChange(callback) {
    try {
      const handleNetworkChange = async (newNetwork) => {
        callback({
          name: newNetwork,
          chainId: void 0,
          api: void 0
        });
      };
      await this.provider?.onNetworkChange(handleNetworkChange);
    } catch (error) {
      const errMsg = error.message;
      throw errMsg;
    }
  }
  async onAccountChange(callback) {
    try {
      const handleAccountChange = async (newAccount) => {
        if (newAccount?.publicKey) {
          callback({
            ...newAccount
          });
        } else {
          const response = await this.connect();
          callback({
            ...response
          });
        }
      };
      await this.provider?.onAccountChange(handleAccountChange);
    } catch (error) {
      console.log(error);
      const errMsg = error.message;
      throw errMsg;
    }
  }
  async network() {
    try {
      const response = await this.provider?.network();
      if (!response)
        throw `${MartianWalletName} Network Error`;
      return {
        name: response,
        chainId: this.networkToChainId[response.toLowerCase()]
      };
    } catch (error) {
      throw error;
    }
  }
};
export {
  MartianWallet,
  MartianWalletName
};
